          บันทึก&nbsp;<i class="bi bi-check-lg"></i>
        </button>
        <button class="btn btn-dark" type="button" data-bs-toggle="collapse" data-bs-target=".multi2"
          v-on:click="[(p_processbar = 50), (bg_pb3 = 'secondary')]">
          <i class="bi bi-chevron-left"></i>&nbsp;กลับ
        </button>
      </div>
    </div>
