<script setup>
import { ref, reactive, computed, onMounted, inject } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";

const store = useStore();
const router = useRouter();
const m_topic = ref("");
const s_topic = ref("");
const detail = ref("");
const date1 = ref(new Date());
const date2 = ref(new Date());
const keyword = ref("");
const check1 = ref("No");
const check2 = ref("Register");
const latitude = ref("");
const longitude = ref("");
const auto_number = ref("");
const day = ref("");
const month = ref("");
const year = ref("");
const hours = ref("");
const min = ref("");
const p_processbar = ref("0");
const bg_pb1 = ref("primary");
const bg_pb2 = ref("secondary");
const bg_pb3 = ref("secondary");
const file = ref(File | null);
const form = ref(HTMLFormElement);
//include fuction root page App.vue
const toast = inject("toast");
const go_to_route_name = inject("gtr_name");
const go_to_route_path = inject("gtr_path");

onMounted(() => {
  set_upload();
  set_location();
  format(new Date());
  auto_number.value = gennumber();
});

const format = (date) => {
  day.value = date.getDate();
  month.value = date.getMonth() + 1;
  year.value = date.getFullYear();
  hours.value = date.getHours();
  min.value = date.getMinutes();
};
const set_test = () => {
  detail.value = "ทดสอบนำเข้าข้อมูล";
  m_topic.value = "2";
  s_topic.value = "4";
  date2.value = "2050-12-31 23:59:59";
  check1.value = "No";
  check2.value = "All";
};
const save_rapsarb = () => {
  var arr = reactive({
    login_eid: encodeURIComponent(store.getters.get_eid),
    auto_num: auto_number.value,
    m_topic: m_topic.value,
    s_topic: s_topic.value,
    detail: detail.value,
    date1: moment(date1.value).format("YYYY-MM-DD HH:mm:ss"),
    date2: moment(date2.value).format("YYYY-MM-DD HH:mm:ss"),
    check1: check1.value,
    check2: check2.value,
    latitude: latitude.value,
    longitude: longitude.value,
    keyword: keyword.value,
    register_no: store.getters.get_simulate_db.length + 1,
  });

  toast("แจ้งเตือน !", "บันทึกเรียบร้อย", "bg-success");
  store.commit("change_simulate_db", arr);
  // console.log(arr);
  setTimeout(() => {
    go_to_route_path("qr_list_topic", "qr_create_topic");
  }, 1000);
};
const gennumber = () => {
  var autoNum =
    "FILE" +
    year.value +
    "" +
    month.value +
    "" +
    day.value +
    "-" +
    Math.floor(1000 + Math.random() * 9000);
  return autoNum;
};

const set_location = () => {
  if (navigator.geolocation) {
    navigator.geolocation.getCurrentPosition(function (position) {
      latitude.value = position.coords.latitude;
      longitude.value = position.coords.longitude;
      // console.log('Latitude Longitude :',latitude.value,longitude.value);
    });
  } else {
    console.log("GeoLocation is Not Supported By This Browser.");
  }
};
const set_upload = () => {
  $("#input_file")
    .fileinput({
      theme: "bs5",
      // uploadUrl: store.getters.get_link + "api/file/upload.php",
      allowedFileExtensions: [
        "jpg",
        "jpeg",
        "png",
        "xls",
        "xlsx",
        "pdf",
        "doc",
        "docx",
      ],
      minFileCount: 1,
      maxFileCount: 6,
      overwriteInitial: false,
      initialPreviewAsData: true,
      maxFileSize: 10000,
      removeFromPreviewOnError: true,
      preferIconicPreview: false,
      previewFileExtSettings: {
        doc: function (ext) {
          return ext.match(/(doc|docx)$/i);
        },
        xls: function (ext) {
          return ext.match(/(xls|xlsx)$/i);
        },
        ppt: function (ext) {
          return ext.match(/(ppt|pptx)$/i);
        },
        zip: function (ext) {
          return ext.match(/(zip|rar|tar|gzip|gz|7z)$/i);
        },
        htm: function (ext) {
          return ext.match(/(htm|html)$/i);
        },
        txt: function (ext) {
          return ext.match(/(txt|ini|csv|java|php|js|css)$/i);
        },
        mov: function (ext) {
          return ext.match(/(avi|mpg|mkv|mov|mp4|3gp|webm|wmv)$/i);
        },
        mp3: function (ext) {
          return ext.match(/(mp3|wav)$/i);
        },
      },
      fileActionSettings: {
        showUpload: false,
        showDownload: false,
        showZoom: false,
        showDrag: true,
      },
      uploadExtraData: function () {
        return {
          id: auto_number.value,
          eid: store.getters.get_eid,
        };
      },
      browseLabel: "เลือกไฟล์",
      browseClass: "btn btn-primary",
      uploadLabel: "อัพโหลด",
      uploadClass: "btn btn-success active",
      removeLabel: "ล้าง",
      removeClass: "btn btn-danger",
    })
    .on("filebatchpreupload", function (event, data, id, index) {
      var n = data.files.length;
      files = n > 1 ? n + " ไฟล์" : "1 ไฟล์";
      if (!window.confirm("คุณต้องการ อัพโหลดไฟล์ จำนวน " + files + "?")) {
        return {
          message: "Upload aborted!", // upload error message
          data: {}, // any other data to send that can be referred in `filecustomerror`
        };
      }
    })
    .on("fileuploaded", function (event, data) {
      // console.log(data.response);
      if (data.response.success == "success" && data.response.upload == "yes") {
        toast("แจ้งเตือน !", "บันทึกไฟล์เรียบร้อย", "bg-success");
      } else if (
        data.response.success == "success" &&
        data.response.upload == "yes"
      ) {
        toast(
          "แจ้งเตือน !",
          "บันทึกไฟล์เรียบร้อย แต่บันทึกที่อยู่ไม่ได้",
          "bg-warning"
        );
      } else {
        toast("แจ้งเตือน !", "ผิดพลาด", "bg-danger");
      }
    });
};
</script>
